import React, { useCallback } from 'react';
import { shallowEqual, useDispatch, useSelector } from 'react-redux';
import { switchtheme } from '../../store/sidebar/action';
import './style.less';


const AppTheme = () => {
    const { showTheme } = useSelector((state) => ({
        showTheme: state.sidebar.showTheme,
    }), shallowEqual);

    const dispatch = useDispatch();
    const handleSwitch = useCallback((value) => {
        dispatch(switchtheme(value));
    }, [dispatch]);

    return (
        <section className={['theme-box', showTheme && 'theme-box-show'].join(' ')}>
            <div className="theme">
                <div className="theme-title">
                    点击切换主题色
                </div>
                <div className="theme-list" onClick={() => { handleSwitch('cyan') }}>
                    <span style={{ background: '#13c2c2' }}></span>明青
                </div>
                <div className="theme-list" onClick={() => { handleSwitch('blue') }}>
                    <span style={{ background: '#40a9ff' }}></span>拂晓蓝
                </div>
                <div className="theme-list" onClick={() => { handleSwitch('green') }}>
                    <span style={{ background: '#52c41a' }}></span>极光绿
                </div>
                <div className="theme-list" onClick={() => { handleSwitch('orange') }}>
                    <span style={{ background: '#ffc53d' }}></span>金盏花
                </div>
                <div className="theme-list" onClick={() => { handleSwitch('pink') }}>
                    <span style={{ background: '#ffadd2' }}></span>法式洋红
                </div>

            </div>
        </section>
    );
}

export default AppTheme;