import React, { useCallback } from 'react';
import { shallowEqual, useDispatch, useSelector } from 'react-redux';
import { collapse, handletheme } from '../../store/sidebar/action';
import { changeVisible, changeType } from '../../store/dialog/action';
import './style.less';


const Sidebar = (props) => {
    const { collapsed } = useSelector((state) => ({
        collapsed: state.sidebar.collapsed,
    }), shallowEqual);
    
    const dispatch = useDispatch();

    const handleChange = useCallback(() => {
        dispatch(collapse(false));
        dispatch(handletheme(true));
    }, [dispatch]);

    const handleDownload = useCallback(() => {
        const aTag = document.createElement('a');
        const blob = new Blob([localStorage.getItem('react_notepad')]);
        aTag.download = 'data.txt';
        aTag.href = URL.createObjectURL(blob);
        aTag.click();
        URL.revokeObjectURL(blob);
    }, []);

    const handleClear = useCallback(() => {
        dispatch(changeVisible(true));
        dispatch(collapse(false));
        dispatch(changeType('clear'));
    }, [dispatch]);

    return (
        <div className={['event-tools', collapsed && 'show-event-tools'].join(' ')}>
            <ul className="tools-sidebar">
                <li>
                    <button className="tools-btn" onClick={handleChange}>切换主题</button>
                </li>
                {/* <li>
                    <button className="tools-btn" onClick={handleDownload}>下载数据</button>
                </li> */}
                <li>
                    <button className="tools-btn" onClick={handleClear}>清空数据</button>
                </li>
            </ul>
        </div>
    )
};

export default Sidebar;